@charset "UTF-8";
/*Обнуление*/
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*, *:before, *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
}

/*----------------------------------------------------*/
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  src: url("dist/fonts/OpenSans-Light.woff2") format("woff2"), url("dist/fonts/OpenSans-Light.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("dist/fonts/OpenSans-Regular.woff2") format("woff2"), url("dist/fonts/OpenSans-Regular.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  src: url("dist/fonts/OpenSans-SemiBold.woff2") format("woff2"), url("dist/fonts/OpenSans-SemiBold.woff") format("woff");
}
/*-----------------------------------------------------------------------------------*/
body {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
}

.container {
  max-width: 1171px;
  margin: 0 auto;
}

/*-----------------------------------------------*/
/*HEADER*/
.header {
  padding: 30px 0;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__container img {
  width: 115px;
  height: 44px;
}

.nav {
  padding-left: 100px;
}

.nav__list {
  display: flex;
  align-items: center;
  flex-flow: wrap row;
}
.nav__list li:not(:last-child) {
  margin-right: 52px;
}
.nav__list a {
  font-size: 15px;
  line-height: 1.86;
  font-weight: 600;
  color: #1c1c1c;
}
.nav__list a:hover {
  padding-bottom: 39px;
  border-bottom: 4px solid #3172b9;
  color: #3172b9;
  transition: all 0.2s ease;
}
.nav__list a:focus {
  outline: 2px solid #3172b9;
  border: 0;
  padding: 0;
}
.nav__list a:active {
  color: black;
  transition: color 0.1s ease-out;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  line-height: 1.4;
  font-weight: 600;
  letter-spacing: 0.005em;
  color: #ffffff;
  background-color: #3172b9;
  border-radius: 4px;
}
.btn:hover {
  opacity: 0.8;
  transition: opacity 0.2s ease;
}
.btn:focus {
  outline: 2px solid #3172b9;
}
.btn:active {
  color: black;
  transition: color 0.1s ease-out;
}

.hamburger-menu {
  display: none;
}

.header__btn {
  flex: 0 0 150px;
  min-height: 50px;
}

/*TRIAL*/
.trial {
  background: url("../img/trial/trial-background.jpg") no-repeat right center;
  background-size: cover;
  padding-bottom: 134px;
}

.trial-box {
  display: flex;
  flex-direction: column;
  padding-top: 124px;
  max-width: 520px;
}

.trial-box__title {
  font-size: 40px;
  line-height: 1.3;
  font-weight: 300;
  letter-spacing: 0.01em;
  color: #1c1c1c;
  margin-bottom: 32px;
}

.trial-box__subtitle {
  line-height: 1.56;
  font-weight: 400;
  letter-spacing: 0.02em;
  color: #787878;
  margin-bottom: 36px;
}

.trial-subbox {
  display: flex;
  flex-direction: column;
  max-width: 250px;
}

.trial-subbox__btn {
  font-size: 17px;
  line-height: 1.23;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.005em;
  min-width: 250px;
  height: 58px;
  margin-bottom: 7px;
}

.trial-subbox__text {
  font-size: 11px;
  line-height: 1.9;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.005em;
  color: #787878;
  align-self: center;
  background: url("../img/trial/trial-shield.svg") no-repeat left center;
  padding-left: 20px;
}

/*DO*/
.do {
  padding-top: 225px;
  padding-bottom: 197px;
  border-bottom: 1px solid #dadada;
}

.do-box {
  text-align: center;
}
.do-box h5 {
  font-size: 36px;
  line-height: 0.58;
  font-weight: 300;
  letter-spacing: 0.005em;
  color: #1c1c1c;
  margin-bottom: 36px;
}
.do-box p {
  line-height: 1.31;
  font-weight: 400;
  letter-spacing: 0.03em;
  color: #787878;
  margin-bottom: 55px;
}

.do-list {
  display: flex;
  flex-wrap: wrap;
  flex: 0 1 25%;
  justify-content: space-between;
}
.do-list li {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 0 1 270px;
  min-height: 320px;
  border: 1px solid #e4e4e4;
  border-radius: 5px;
  cursor: pointer;
}

.do-list__item:hover {
  border: 3px solid #3172b9;
  background-color: #3172b9;
  transition: all 0.2s ease;
}
.do-list__item:hover:focus {
  outline: 2px solid #3172b9;
}
.do-list__item:hover:hover h5, .do-list__item:hover:hover p {
  color: #ffffff;
  transition: color 0.2s ease;
}
.do-list__item:hover:hover svg path {
  fill: white;
  transition: fill 0.2s ease;
}

.do-list__img {
  margin-bottom: 33px;
}

h5 {
  font-size: 20px;
  line-height: 1.05;
  font-weight: 600;
  color: #1c1c1c;
  margin-bottom: 20px;
}

p {
  font-size: 14px;
  line-height: 1.5;
  font-weight: 400;
  color: #787878;
}

.do-list__img__size1 {
  width: 68px;
  height: 66px;
}

.do-list__img__size2 {
  width: 86px;
  height: 64px;
}

.do-list__img__size3 {
  width: 64px;
  height: 64px;
}

.do-list__img__size4 {
  width: 65px;
  height: 65px;
}

/*CLIENTS*/
.clients {
  padding-top: 97px;
  padding-bottom: 161px;
}

.clients__header {
  font-size: 36px;
  line-height: 0.77;
  font-weight: 300;
  letter-spacing: 0.02em;
  color: #414141;
  margin-bottom: 43px;
}

.clients-list {
  display: flex;
  flex-wrap: wrap;
  flex: 0 1 33.333%;
  justify-content: space-between;
}
.clients-list li {
  flex: 0 1 370px;
  min-height: 362px;
  border: 1px solid #dadada;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.clients-item__text {
  padding: 57px 0 0 37px;
  font-size: 14px;
  line-height: 1.78;
  font-weight: 400;
  color: #787878;
}

.clients-item__user {
  display: flex;
  border-top: 1px solid #dadada;
  padding: 27px 0 32px 37px;
}
.clients-item__user .clients-item__img {
  width: 67px;
  height: 68px;
  border: 2px solid #3172b9;
  border-radius: 50px;
  background-color: #787878;
  margin-right: 16px;
}

.clients-item-user-desc {
  align-self: center;
}
.clients-item-user-desc .clients-item-user-desc__name {
  font-size: 17px;
  line-height: 1.47;
  font-weight: 600;
  color: #1c1c1c;
}
.clients-item-user-desc .clients-item-user-desc__job {
  font-size: 14px;
  line-height: 1.78;
  font-weight: 400;
  color: #787878;
  opacity: 0.6;
}

/*CONTACTS*/
.contacts {
  padding: 69px 0 168px 0;
  border-top: 1px solid #dadada;
  border-bottom: 1px solid #dadada;
}

.contacts__header {
  font-size: 36px;
  line-height: 0.77;
  font-weight: 300;
  letter-spacing: 0.02em;
  color: #414141;
  margin-bottom: 120px;
}

.contacts__list {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
}
.contacts__list li {
  flex: 0 0 207px;
}
.contacts__list li:not(:last-child) {
  margin-right: 194px;
}
.contacts__list img {
  margin-bottom: 29px;
}
.contacts__list h6 {
  font-size: 17px;
  line-height: 1.47;
  font-weight: 600;
  color: #1c1c1c;
  margin-bottom: 44px;
}
.contacts__list p {
  font-size: 14px;
  line-height: 1.78;
  font-weight: 400;
  color: #787878;
}

.contacts-item__img_size1 {
  width: 19px;
  height: 26px;
}

.contacts-item__img_size2 {
  width: 26px;
  height: 26px;
}

.contacts-item__img_size3 {
  width: 29px;
  height: 19px;
}

.contacts-item-phones {
  display: flex;
  flex-direction: column;
}
.contacts-item-phones a {
  font-size: 14px;
  line-height: 1.78;
  font-weight: 400;
  color: #787878;
}
.contacts-item-phones a:hover {
  color: #3172b9;
  transition: color 0.2s ease;
}
.contacts-item-phones a:focus {
  outline: 2px solid #3172b9;
}
.contacts-item-phones a:active {
  color: black;
  transition: color 0.1s ease-out;
}

.contacts-item-mails {
  display: flex;
  flex-direction: column;
}
.contacts-item-mails a {
  font-size: 14px;
  line-height: 1.78;
  font-weight: 400;
  color: #787878;
}
.contacts-item-mails a:hover {
  color: #3172b9;
  transition: color 0.2s ease;
}
.contacts-item-mails a:focus {
  outline: 2px solid #3172b9;
}
.contacts-item-mails a:active {
  color: black;
  transition: color 0.1s ease-out;
}

/*started*/
.started {
  padding: 118px 0;
  border-bottom: 1px solid #dadada;
}

.started__box {
  text-align: center;
}
.started__box h5 {
  font-size: 36px;
  line-height: 0.58;
  font-weight: 300;
  letter-spacing: 0.005em;
  color: #1c1c1c;
  margin-bottom: 28px;
}
.started__box p {
  line-height: 1.31;
  font-weight: 400;
  letter-spacing: 0.03em;
  color: #787878;
  margin-bottom: 60px;
}

.started__form {
  display: flex;
  justify-content: center;
}
.started__form input {
  flex: 0 1 251px;
  height: 60px;
  border: 1px solid #dadada;
  border-radius: 30px;
  padding-left: 15px;
}
.started__form input:focus {
  border: 2px solid #3172b9;
}

.started__input:not(:last-child) {
  margin-right: 19px;
}

.started__btn {
  flex: 0 0 230px;
  height: 60px;
  background-color: #3172b9;
  border-radius: 30px;
}

/*FOOTER*/
.footer {
  padding: 45px 0;
}

.footer__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer-left {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
}
.footer-left img {
  width: 115px;
  height: 44px;
  margin-right: 26px;
}
.footer-left p {
  font-size: 12px;
  line-height: 1.41;
  font-weight: 400;
  color: #787878;
}

.footer-nav {
  margin-right: 59px;
}

.footer-nav__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.footer-nav__list li:not(:last-child) {
  margin-right: 38px;
}
.footer-nav__list a {
  font-size: 14px;
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: 0.01em;
  color: #787878;
}

.footer-nav__link:hover {
  color: #3172b9;
  transition: color 0.2s ease;
}

.footer-nav__link:focus {
  outline: 2px solid #3172b9;
}

.footer-nav__link:active {
  color: black;
  transition: color 0.1s ease-out;
}

.footer__right {
  display: flex;
}

.footer-icons {
  display: flex;
  align-items: center;
}
.footer-icons a {
  opacity: 0.63;
}
.footer-icons a:not(:last-child) {
  margin-right: 30px;
}

.footer-icons__icon_size1 {
  width: 19px;
  height: 17px;
}

.footer-icons__icon_size2 {
  width: 20px;
  height: 20px;
}

.footer-icons__icon_size3 {
  width: 25px;
  height: 18px;
}

.footer-icons__icon:focus {
  outline: 2px solid #3172b9;
}

@media (max-width: 1200px) {
  .container {
    padding: 0 15px;
  }
  .do__list {
    align-items: center;
    justify-content: center;
  }
  .clients__list {
    align-items: center;
    justify-content: center;
  }
  .clients__header {
    text-align: center;
  }
  .contacts__header {
    text-align: center;
  }
  .contacts__list {
    justify-content: center;
  }
  .contacts-item {
    max-width: 150px;
  }
  .clients__list {
    gap: 10px;
  }
}
@media (max-width: 992px) {
  .trial__container {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .do__list {
    gap: 10px;
  }
  .trial-box__subtitle {
    color: #000000;
  }
  .trial-subbox__text {
    color: #000000;
  }
  .footer-left__img {
    margin-left: 4px;
    margin-right: 0;
    margin-bottom: 10px;
  }
  .contacts__list {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .contacts-item {
    flex: 0 0 130px;
    text-align: center;
  }
  .contacts__list li:not(:last-child) {
    margin-right: 0;
    margin-bottom: 20px;
  }
  .started__form {
    flex-direction: column;
    align-items: center;
  }
  .started__input:not(:last-child) {
    margin-right: 19px;
    margin-bottom: 10px;
  }
  .started__input {
    max-width: 251px;
    max-height: 60px;
  }
  .started__btn {
    margin-top: 20px;
    min-width: 230px;
    max-height: 60px;
  }
}
@media (max-width: 768px) {
  .hamburger-menu {
    display: block;
  }
  #menu__toggle {
    opacity: 0;
  }
  .menu__btn {
    display: flex;
    align-items: center;
    position: fixed;
    top: 40px;
    left: 40px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 5;
  }
  .menu__btn > span,
.menu__btn > span::before,
.menu__btn > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #616161;
  }
  .menu__btn > span::before {
    content: "";
    top: -8px;
  }
  .menu__btn > span::after {
    content: "";
    top: 8px;
  }
  .menu__box {
    display: block;
    position: fixed;
    visibility: hidden;
    top: 0;
    left: -100%;
    width: 300px;
    height: 100%;
    margin: 0;
    padding: 80px 0;
    list-style: none;
    text-align: center;
    background-color: #ECEFF1;
    box-shadow: 1px 0 6px rgba(0, 0, 0, 0.2);
  }
  .menu__item {
    display: block;
    padding: 12px 24px;
    color: #333;
    font-family: "Roboto", sans-serif;
    font-size: 20px;
    font-weight: 600;
    text-decoration: none;
  }
  .menu__item:hover {
    background-color: #CFD8DC;
  }
  #menu__toggle:checked ~ .menu__btn > span {
    transform: rotate(45deg);
  }
  #menu__toggle:checked ~ .menu__btn > span::before {
    top: 0;
    transform: rotate(0);
  }
  #menu__toggle:checked ~ .menu__btn > span::after {
    top: 0;
    transform: rotate(90deg);
  }
  #menu__toggle:checked ~ .menu__box {
    visibility: visible;
    left: 0;
  }
  .menu__btn > span,
.menu__btn > span::before,
.menu__btn > span::after {
    transition-duration: 0.25s;
  }
  .menu__box {
    background-color: #ffffff;
    z-index: 2;
    transition-duration: 0.25s;
  }
  .menu__item {
    transition-duration: 0.25s;
  }
  .trial {
    opacity: 0.8;
  }
  .nav__item {
    display: none;
  }
  .nav {
    display: none;
  }
  .footer-left__copyrights {
    display: none;
  }
  .header__logo-link {
    padding-left: 80px;
  }
}
@media (max-width: 490px) {
  .trial-box__title {
    font-size: 30px;
  }
  .trial-box__subtitle {
    font-size: 12px;
  }
  .footer-icons {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .footer-icons a:not(:last-child) {
    margin-right: 0;
    margin-bottom: 30px;
  }
  .started__box h5 {
    line-height: 1;
  }
  .footer__container {
    align-items: center;
    justify-content: center;
  }
}
@media (max-width: 320px) {
  .header__container img {
    width: 90px;
    height: 30px;
  }
  .header__btn {
    margin-left: 5px;
    flex: 0 1 80px;
    font-size: 10px;
  }
  .menu__btn {
    margin-right: 40px;
  }
  .menu__toggle {
    padding-right: 20px;
  }
  .clients__header {
    line-height: inherit;
  }
  .started__box h5 {
    font-size: 25px;
  }
  .footer-left img {
    width: 90px;
    height: 30px;
  }
  .footer-nav__list {
    display: none;
  }
}